/* General tag level selectors
-----------------------------------------------------------------------------*/
html,
body,
#left-side-middle-bg,
#top-right-corner-bg
{
    height: 100%;
    width: 100%;
}
body
{
    background-color: #efefef;
	font-family: $general-fonts;
    position: relative;
}
a
{
    color: inherit;
    font-family: inherit;
}
a:hover
{
    text-decoration: none;
}
h1,h2,h3,h4,h5,h6,p
{
	margin-top: 0;
}
h1,h2,h3,h4,h5,h6,strong,
.myh1,
.myh2
{
    font-family: $header-fonts;
    letter-spacing: -1px;
}
blockquote {
    color: #333;
    font-style: italic;
    margin-left: 6vw;
    margin-right: 6vw;
}
code
{
	font-size: 14px;
	line-height: 1.2;
}
iframe {
    border: none;
}

/* CONTENT
-----------------------------------------------------------------------------*/
#content-box
{
    background-color: #fff;
    position: relative;
}
    #main-section
    {
        padding: 70px 0;
        position: relative;
    }

    /* Cloud Animation & decorations */
    #floating-cloud-1
    {
        animation: cloud1 130s cubic-bezier($easing);
        animation-iteration-count: infinite;
        height: 50px;
        left: -95px;
        position: fixed;
        top: 120px;
        width: 94px;
        -webkit-animation-iteration-count: infinite;
        -webkit-animation: cloud1 130s cubic-bezier($easing);
    }
    @keyframes cloud1
    {
        0%
        {
            left: -95px;
        }
        100%
        {
            left: 100%;
        }
    }
    @-webkit-keyframes cloud1
    {
        0%
        {
            opacity: 1;
        }
        100%
        {
            left: 100%;
        }
    }
    #left-side-middle-bg
    {
        background-image: url('http://media.clifjackson.net/images/rainbow_corner-left-BW-340x137.png');
        background-position: left 42%;
        background-repeat: no-repeat;
        background-size: 28%;
        left: 0;
        position: fixed;
        top: 0;
    }
    #top-right-corner-bg
    {
        background-image: url('http://media.clifjackson.net/images/rainbow_top-right-BW-142x80.png');
        background-position: right 41px;
        background-repeat: no-repeat;
        background-size: 12%;
        right: 0;
        position: fixed;
        top: 0;
    }

#top-link-box
{
	padding: 10px 0;
	text-align: right;
}

.credits-box
{
    text-align: center;
}

blockquote > .quote-src
{
    font-size: 14px;
    text-align: right;
    width: 100%;
    display: inline-block;
}

/* GENERAL CLASS LEVEL SELECTORS
------------------------------------------------------------------------------*/
.clearfix:after
{
	visibility: hidden;
	display: block;
	font-size: 0;
	content: " ";
	clear: both;
	height: 0;
}
.clouds
{
    background: #f9f9f9 url('http://media.clifjackson.net/images/rainbow_top-right-BW-142x80.png') no-repeat fixed right 40px;
}
.download-link
{
    background-image: url('http://media.clifjackson.net/images/icons/arrow-down-24-sprite.png');
    background-position: 0 -24px;
    background-repeat: no-repeat;
    padding-left: 28px;
}
.download-link:hover
{
    background-position: 0 0;
}
.highlight-yellow
{
	background-color: yellow;
}
.line-height-1
{
	line-height: 1;
}
.line-height-1half
{
	line-height: 1.5;
}
.myh1
{
    font-size: 24px !important;
    font-weight: bold;
}
.myh2
{
    font-size: 20px !important;
    font-weight: bold;
}
.bold-link
{
    font-weight: bold;
    text-decoration: underline;
}
.textcenter
{
    text-align: center;
}
.top40
{
	padding-top: 40px;
}

/* BODY FADE
------------------------------------------------------------------------------*/
.body-fade-in
{
    opacity: 1;
}
.body-fade-out
{
    opacity: 0;
}

/* SAMPLES IFRAME
------------------------------------------------------------------------------*/
.samples-iframe
{
	border: none;
}

.thumbnail-gallery > .thumb-link
{
	text-decoration: none;
}

#gallery-modal-root
{
    background-color: rgba(0, 0, 0, 0.92);
    left: -10000px;
    opacity: 0;
    position: fixed;
    top: 0;
    transition: opacity 0.5s;
	-webkit-transition: opacity 0.5s;
	-webkit-transform: translateZ(0);
}

#beta-flag
{
    height: 70px;
    left: 0;
    position: fixed;
    top: 41px;
    width: 70px;
}

/* VIDEO IFRAMES
+------------------------------------------------------------------------------*/
.responsive-iframe-container {
    position: relative;
    height: 0;
    overflow: hidden;
}
.responsive-iframe-container-16x9 {
    padding-bottom: 56.25%;
}
.responsive-iframe-container > iframe {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
}
